body {
    overflow-x: hidden;
}

.portada {
    width: 100vw;
    height: auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

.howtodo{
    height: auto;
    width: 60%;
    background-color: #000000;
    color: white;
    margin: 0px 20%;

