    text-align: center;
    padding: 15px 25px;
    font-family: "Andika", sans-serif;
    border-bottom: 2px solid #0CF25D ;
    margin-bottom: 30px;
    font-size: 14px;

}
.logo {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo img {
    width: 300px;
    height: auto;
}

.fotoportada {
    width: 85%;
    height: 60vh;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.fotoportada img {
    width: 100%;
    min-height: 500px;
    position: relative;
    object-fit: cover;
    filter: opacity(30%);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.textoportada {
    position: absolute;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.textoportada h2 {
    font-size: 50px;
