/* The Modal (background) */
.modal {
    display: block;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.719);
}

/* Modal Content */
.modal-content {
    background: linear-gradient(147deg, #023535, #015958, #042940);
    margin: 10% auto;
    padding: 35px;
    width: 850px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    border-radius: 25px;
}



